body {
  overflow-x: hidden;
  color: white;
}

html {
  scroll-behavior: smooth;
}

@media only screen and (max-width: 700px) {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  P {
    font-size: 0.8rem;
  }
}

.nav-bar-container {
  display: flex;
  float: right;
  margin-left: 10%;
}

@media only screen and (max-width: 600px) {
  .nav-bar-container {
    display: flex;
    float: right;
    margin-left: 10%;
  }
}

.logo-container {
  width: 60%;
  height: auto;
  margin: auto;
  display: flex;
  flex-direction: row;
  margin-top: 25vh;
}

#landing {
  height: 80vh;
}

@media only screen and (min-width: 2200px) {
  .logo-container {
    margin-top: 10vh;
  }

  #landing {
    height: 100vh;
  }
}

.logo-left {
  width: 30%;
  transform: translate(10%, 0);
}

.logo-right {
  width: 70%;
  transform: translate(-10%, 0);
}

.logo-child {
  z-index: 2;
}

.logo-child > img {
  max-width: 100%;
  max-height: 100%;
  filter: drop-shadow(-3px 3px 0px rgb(0, 0, 0, 0.4));
}

.logo-text {
  margin-left: 20%;
  font-style: italic;
  filter: drop-shadow(-2px 2px 0px rgb(0, 0, 0, 0.4));
}

.logo-text > h2 {
  font-size: 3vw;
  font-weight: 900;
}

.countdown-container {
  width: 25%;
  font-size: 3vw;
}

.date-text {
  font-size: 2.5vw;
  filter: drop-shadow(-2px 2px 0px rgb(0, 0, 0, 0.4));
}

@media only screen and (max-width: 700px) {
  .logo-container {
    width: 90%;
  }

  .logo-text > h2 {
    font-size: 5vw;
    font-weight: 900;
  }

  .date-text {
    font-size: 4vw;
  }
}

.section-container {
  width: 60%;
  text-align: center;
  margin: auto;
  border-radius: 14px;
  box-shadow: 7px 7px 7px rgb(0 0 0 / 20%);
  padding: 1.25rem;
  margin-bottom: 5%;
}

@media only screen and (max-width: 700px) {
  .section-container {
    width: 80%;
  }

  .countdown-container {
    width: 80%;
    font-size: 3em;
  }
}

.section-title-container {
  display: flex;
  align-items: center;
  margin: auto;
  width: fit-content;
  justify-content: center;
}

.section-title-container > img {
  height: 7%;
  width: 7%;
  margin: 2%;
}

.section-title {
  font-weight: 900;
  font-style: italic;
  text-align: center;
  padding-top: 2%;
}

.section-text {
  padding-left: 10%;
  padding-right: 10%;
}

.tracks-container {
  flex-direction: "row";
}

@media only screen and (max-width: 700px) {
  .tracks-container {
    flex-direction: column;
  }
}

.track {
  /*box-shadow: 0 0 1px 1px;*/
  text-align: center;
}

.track:hover {
  box-shadow: 0 0 5px 1px;
}

.footer-links {
  display: flex;
  flex-direction: column;
  text-align: right;
  padding-right: 5%;
  padding-bottom: 5%;
  padding-top: 5%;
  margin-top: 5%;
  width: auto;
}

.footer-text {
  width: 40%;
  margin-left: 5%;
  margin-top: 5%;
  margin-right: 0;
  float: left;
}

.footer-img {
  width: 5%;
  height: 5%;
  margin-left: 1%;
}

@media only screen and (max-width: 700px) {
  .footer-links {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-top: 5%;
    font-size: 0.8rem;
  }

  .footer-links > h2 {
    font-size: 1.5rem;
  }

  .footer-text {
    width: 80%;
    margin-left: 10%;
    margin-right: 10%;
    text-align: center;
  }
  .footer-img {
    width: 10%;
    height: 10%;
    margin-left: 5%;
  }
}

.page-title {
  font-weight: bold;
  font-style: italic;
  text-align: center;
  font-size: 4em;
}

.schedule-table {
  margin: auto;
  width: 80%;
}

.schedule-table > tr > td {
  padding: 2%;
  margin: 5%;
  color: black;
  width: 40%;
}

.schedule-table > tr > :nth-child(2) {
  text-align: right;
  font-weight: normal;
}

.schedule-table > tr > td:first-child {
  border-left: none;
}

.schedule-table > tr > td:last-child {
  border-right: none;
}

.schedule-table > tr:first-child > td {
  border-top: none;
}

.schedule-table > tr:last-child > td {
  border-bottom: none;
}

.days {
  display: flex;
  width: 60%;
  margin: auto;
}

.day {
  box-shadow: 7px 7px 7px rgb(0 0 0 / 25%);
}

.day:hover {
  box-shadow: 7px 7px 7px rgb(0 0 0 / 75%);
}

.schedule-div {
  width: 60%;
}

@media only screen and (max-width: 700px) {
  .days {
    width: 80%;
  }

  .schedule-div {
    width: 80%;
  }

  .schedule-table {
    margin: auto;
    width: 90%;
  }
}

.faq-pill-col {
  paddingbottom: "24px";
}

.faq-pill-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.faq-pill-q {
  width: 280px;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media only screen and (max-width: 900px) {
  .faq-pill-col {
    flex-direction: column;
    align-items: center;
  }

  .faq-pill-row {
    flex-direction: column;
    align-items: center;
  }

  .faq-pill {
    margin-right: 0;
  }

  .faq-pill-q {
  }
}

.sponsor-container {
  width: 80%;
  height: 30vh;
}

.fade-in {
  opacity: 1;
  transition: width 0.5s, height 0.5s, opacity 0.5s 0.5s;
  position: absolute;
  z-index: 10;
}

.fade-out {
  opacity: 0;
  transition: width 0.5s 0.5s, height 0.5s 0.5s, opacity 0.5s;
  position: absolute;
  z-index: 0;
}

@media only screen and (max-width: 700px) {
  .sponsor-container {
    width: 90%;
  }
}

.scroll-animation {
  transition: transform 0.3s ease-in-out;
}

.grow {
  transform: scale(1.5);
}
